import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import '../Styles/LandingProducts.css';

function LandingProducts() {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  
  const API_BASE_URL = "https://home-back-3lqs.onrender.com";

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await fetch(`${API_BASE_URL}/products`);
        const data = await res.json();
        setProducts(data.products || []);
      } catch (err) {
        console.error("Error loading products:", err);
        setProducts([]);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();

    // Generate noise texture for product cards
    const generateNoise = (opacity = 0.12) => {
      if (!document.createElement('canvas').getContext) {
        return false;
      }

      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext('2d');
      canvas.width = 200;
      canvas.height = 200;

      for (let x = 0; x < canvas.width; x++) {
        for (let y = 0; y < canvas.height; y++) {
          const number = Math.floor(Math.random() * 60);
          ctx.fillStyle = `rgba(${number}, ${number}, ${number}, ${opacity})`;
          ctx.fillRect(x, y, 1, 1);
        }
      }

      document.documentElement.style.setProperty(
        '--noise-texture', 
        `url(${canvas.toDataURL("image/png")})`
      );
    };

    generateNoise();
  }, []);

  // Helper function to truncate description
  const truncateDescription = (text, maxLength = 60) => {
    if (!text) return '';
    if (text.length <= maxLength) return text;
    return text.substring(0, maxLength).trim() + '...';
  };

  const handleViewDetails = (productId) => {
    navigate(`/product/${productId}`);
  };

  const handleAddToCart = (e, product) => {
    e.stopPropagation(); // Prevent card click
    // Add your cart logic here
    console.log('Add to cart:', product);
  };

  if (loading) {
    return (
      <div className="products-container">
        <div className="loading-state">
          <div className="loading-spinner"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="products-container">
      <div className="products-content">
        <div className="products-grid">
          {products.map((product) => (
            <div 
              key={product._id} 
              className="product-card"
              onClick={() => handleViewDetails(product._id)}
            >
              <div className="card-noise-overlay"></div>
              
              <div className="product-image-container">
                {product.mainImage && (
                  <img
                    src={product.mainImage}
                    alt={product.name}
                    className="product-img"
                    loading="lazy"
                  />
                )}
              </div>

              <div className="product-info">
                <h3 className="product-name">{product.name}</h3>
                
                {product.description && (
                  <p className="product-description">
                    {truncateDescription(product.description, 60)}
                  </p>
                )}
                
                <p className="product-price">{product.price?.toFixed(2)}</p>
                
                <div className="product-actions">
                  <button 
                    className="add-to-cart-btn"
                    onClick={(e) => handleAddToCart(e, product)}
                  >
                    <span className="btn-text"> </span>
                  </button>
                  
                  <button 
                    className="details-btn icon-wrapper"
                    onClick={() => handleViewDetails(product._id)}
                  >
                    <span className="icon-copy"></span>
                    <span className="icon icon-after more" aria-hidden="true"></span>
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default LandingProducts;